html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

body {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  display: inline-block;
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

.container_books {
  width: 1000px;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 10px;
  background-color: #e5e5e5;
}

.container {
  width: 1340px;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: flex-start;
}

.container_menu {
  width: 280px;
  padding: 20px;
}

.book_title {
  font-family: "Rubik";
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  color: #000000;
}

.book_box {
  padding: 20px;
  background: #ffffff;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  display: inline-block;
  width: 290px;
}

.bookImg_box {
  background: #f8fafd;
  border-radius: 5px;
  padding: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
}

* {
  box-sizing: border-box;
}

.book_title {
  margin-top: 17px;
  font-family: "Rubik";
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  color: #000000;
}

.book_author,
.book_data {
  font-family: "Noto Sans";
  font-weight: 500;
  font-size: 13px;
  line-height: 18px;
  color: #757881;
  margin-top: 2px;
  margin-bottom: 2px;
}

.book_bookmark {
  background: #ffd80d;
  border-radius: 4px;
  padding: 10px 25px;
  border: none;
  font-family: "Rubik";
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  width: 120px;
}

.book_moreInfo {
  border-radius: 4px;
  padding: 10px 25px;
  border: none;
  font-family: "Rubik";
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #0d75ff;
  background: rgba(13, 117, 255, 0.05);
  margin-left: 6px;
  width: 120px;
}

.book_read {
  background: #75828a;
  border-radius: 4px;
  font-family: "Rubik";
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #ffffff;
  width: 246px;
  padding: 10px;
  border: none;
  margin-top: 15px;
}

button {
  cursor: pointer;
}

.book_box {
  height: 500px;
}

.book_box {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
}

.title {
  font-family: "Rubik";
  font-weight: 400;
  font-size: 24px;
  line-height: 28px;
  color: #000000;
  margin-top: 25px;
  margin-bottom: 5px;
}
.text {
  font-family: "Rubik";
  font-weight: 400;
  font-size: 14px;
  line-height: 155.6%;
  color: #000000;
  margin-bottom: 20px;
}

.bookmark_box {
  background: #f8fafd;
  border-radius: 4px;
  width: 218px;
  padding: 15px;
}

.bookmark_title {
  font-family: "Rubik";
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
  margin-bottom: 5px;
}

.bookmark_text {
  font-family: "Noto Sans";
  font-weight: 500;
  font-size: 13px;
  line-height: 18px;
  color: #757881;
}

.bookmark_btn1,
.bookmark_btn2 {
  border: none;
  background-repeat: no-repeat;
  background-position: center;
  width: 24px;
  height: 24px;
  background-image: url("../../imgs/book-open.svg");
  background-color: transparent;
}

.bookmark_btn2 {
  background-image: url("../../imgs/delete.svg");
  margin-left: 5px;
}

.bookmark_btnBox {
  display: flex;
  align-items: center;
}

.bookmark_box {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.container_menu {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
}

.bookmark_add {
  color: rgb(196, 62, 62);
  font-weight: 700;
}

.right_menu {
  background-color: rgba(0, 0, 0, 0.144);
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
}

.right_menuItem {
  background-color: #fff;
  position: fixed;
  top: 0;
  right: 3px;
  bottom: 0;
  width: 0vw;
  height: 100vh;
  animation-name: right_menu;
  animation-iteration-count: 1;
  animation-duration: 1s;
  overflow-x: hidden;
  padding: 0 20px;
}

@keyframes right_menu {
  0% {
    width: 0vw;
  }
  100% {
    width: 40vw;
  }
}

#rightMenu_width {
  width: 40vw;
}

.display_none {
  display: none;
}

.rightMenu_exit{
  background-color: transparent;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url('../../imgs/x.svg');
  width: 18px;
  height: 18px;
  background-repeat: no-repeat;
}







.right_menuTop {
  width: 110%;
  background: #f8fafd;
  padding: 22px;
  margin-left: -20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.rightMenu_title {
  font-family: "Rubik";
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  color: #222531;
}

.rightMenu_imgBox {
  height: 280px;
  width: 200px;
  margin: 0 auto;
  margin-top: 10px;
}

.rightMenu_description {
  font-family: "Rubik";
  font-weight: 400;
  font-size: 13px;
  line-height: 170.3%;
  color: #58667e;
  margin-top: 10px;
}

.rightMenu_text {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #222531;
}

.rightMenu_span {
  background: rgba(13, 117, 255, 0.09);
  border-radius: 30px;
  padding: 5px 25px;
  font-family: "Rubik";
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #0d75ff;
}

.rightMenu_textBox{
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  gap: 20px;
}

.rightMenu_spanBox{
  display: flex;
  align-items: flex-start;
  gap: 10px;
  flex-direction: column;
}

.rightMenu_alltextBox{
  margin-top: 30px;
  display: flex;
  align-items: baseline;
  gap: 15px;
  margin-bottom: 30px;
}







///////////////////////
/// 



::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.548);
  border-radius: 5px;
}
::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.315);
}




