html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

body {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  display: inline-block;
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

.container {
  max-width: 1340px;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
}

.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.orderby_newest {
  border: none;
  background: #a6b0c3;
  border-radius: 4px;
  padding: 6px;
  cursor: pointer;
}
.orderby_newest::before {
  content: "";
  background-repeat: no-repeat;
  width: 24px;
  height: 24px;
  background-image: url("../../imgs/kalendar.svg");
  display: inline-block;
  vertical-align: middle;
  margin-right: 5px;
}

.header_bottom {
  padding: 10px;
  padding-left: 310px;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.15);
}

.all_result {
  font-family: "Rubik";
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #222531;
  cursor: pointer;
}
