html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

body {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  display: inline-block;
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

.container {
  max-width: 1340px;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
}

.logo_box {
  width: 266px;
}

.search {
  background: #f8fafd;
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.1);
  border-radius: 30px;
  padding: 20px;
  max-width: 470px;
  width: 100%;
  border: none;
  font-family: "Rubik";
  font-weight: 400;
  font-size: 13px;
  line-height: 15px;
  color: rgba(0, 0, 0, 0.24);
}
.search::placeholder {
  background-repeat: no-repeat;
  background-image: url("../../imgs/search.svg");
  background-size: contain;
  padding-left: 30px;
}

.darkmode_box {
  width: 23px;
  height: 23px;
  cursor: pointer;
}

.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header {
  margin-top: 20px;
}

.logout {
  font-family: "Rubik";
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #ffffff;
  padding: 10px 22px;
  background-color: #0D75FF;
  border-radius: 4px;
  border: none;
  cursor: pointer;
}

.header_left{
  display: flex;
  align-items: center;
  gap: 15px;
}

.container{
  padding-bottom: 20px;
} 